body {
  background-color: brown;
}

.App {
  min-height:500px;
  background-color: rgb(129, 39, 12);
  width:400px;
  margin: 40px auto;
  border-radius: 20px;
}

#to-do-form input{
  background-color: rgb(210, 231, 87);
  border: 0;
  width:200px;
  height: 40px;
  padding: 0 20px;
  margin: 20px;
  color: rgb(43, 51, 6);
  border-radius: 10px;
}

#to-do-form input::placeholder {
  color: rgb(43, 51, 6);
}
#to-do-form input:focus, #to-do-form button:focus {
  outline: none;
}

#to-do-form button {
  height: 40px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(117, 111, 25);
  font-weight: bolder;
  color: rgb(43, 51, 6);
}

.list {
  width:330px;
  background-color: rgb(210, 231, 87);
  height: 40px;
  margin: 20px auto;
  border-radius: 5px;
  color: rgb(43, 51, 6);
}

.list p{
  padding: 10px;
  position: relative;
}

.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.faicon {
  margin: 0 5px;
  font-size: 15px;
}

.list p input {
  background-color: transparent;
  border: 0;
  color: rgb(43, 51, 6);
  font-size: 18px;
}

.list p input:focus{
  outline: none;
}

h1{
  color: rgb(172, 199, 51);
  text-align: center;
  margin: 10px auto;
}